import { Component } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {MatSnackBar} from '@angular/material';

export interface DialogData {
  animal: string;
  name: string;
}

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  message: string='hfurhfhrf';
  action: string='hhfbhfbhfb';
  name: string;
  loginForm: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private auth: AuthService,
    private snackBar: MatSnackBar) { }

    openSnackBar(message: string, action: string) {
    this.snackBar.open(message,action,{
    });
  }


  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      regno: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  get f() { return this.loginForm.controls; }

  login() {
    this.auth.userLogin(this.f.regno.value, this.f.password.value)
      .subscribe(
        data => {
          console.log(data['data'].status);
          if (data['data'].role == 'user' && data['data'].status == 'active') {
            this.router.navigateByUrl(`/options`);
          }
          if (data['data'].role == 'admin' && data['data'].status == 'active') {
            this.router.navigateByUrl(`/admin`);
          }
        },
        error => {
          console.log(error.error.message);
        });
    this.loginForm.reset();

  }

}
